.navbar {
    width: 100%;
    height: 80px;
    background-color: rgb(19, 19, 19);
    display: flex;
    align-items: center;
    justify-content: flex-end;
    
    .links {
        margin-right: 50px;
        display: flex;
        align-items: center;
    
        a {
            text-decoration: none;
            color: white;
            margin-left: 20px;
            font-size: 25px;
        }
    }
}
